@import 'constants/constants.sass';

* {
  outline: none !important;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0; }

h1, h2, h3 {
  font-weight: 500; }

h2 {
  font-size: 1.6rem;
  margin-bottom: 15px; }

button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 11px 15px;
  background: #009688;
  color: #fff; }

input {
  font-size: 1.2rem; }

.background {
  background: url('img/bg.jpg');
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1; }
.app {
  position: relative;
  padding: 0 20px;
  margin-bottom: 20px;
  input {
    padding-left: 10px !important; }
  section {
    display: flex; }
  main, aside {
    display: flex;
    flex-direction: column; }
  main {
    width: 65%; }
  .nav-tabs {
    margin-bottom: 10px;
    display: flex;
    .nav-item {
      flex-grow: 1;
      margin-top: 10px; } }
  .delete-wrap {
    position: relative;
    &:hover .delete {
      opacity: 1; }
    .delete {
      width: 25px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $white;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      z-index: 10;
      cursor: pointer;
      opacity: 0;
      svg {
        fill: #000;
        height: 15px;
        width: 15px; } } } }
