@import '../../constants/constants.sass';

.add-event {

  .list {}

  .list-item {
    display: flex;
    align-items: center;
    margin: 10px 0;
    margin-right: 10px;
    padding: 10px;
    padding-right: 50px;
    font-size: 18px;

    .nmb {
      margin-right: 5px; }

    &--error {
      background: $error;
      color: $white; }

    &--check {
      background: $check;
      color: $white; } }

  .list-item__checklist {
    display: flex;
    align-items: center;
    position: relative;
    &:hover {
      background: $check;
      cursor: pointer; }

    .scroller-wrap {
      margin-left: 30px; }

    .check-sign {
      font-size: 0;
      position: absolute;
      left: 10px; } }

  .list-item__mistakes {
    display: flex;
    align-items: center;

    .mistake-cnt {
      width: 30px;
      height: 30px;
      font-size: 15px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $white;
      background: red;
      margin-right: 10px;
      flex-shrink: 0; } } }
