@import '../../constants/constants.sass';

header {
  display: flex;
  align-items: center;
  .timer-wrap {
    flex-shrink: 0; }
  .add {
    width: 50px;
    height: 50px;
    background: $darkgreen;
    border-radius: 50%;
    color: $white;
    cursor: pointer;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0; }
  .h1-page {
    flex-grow: 1;
    h1 {
      text-align: center;
      font-size: 30px; } } }
